import Vue from 'vue'
import Vuex from 'vuex'
import { name } from './name.js'
import { tree } from './tree.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dark: false,
    //replace this with your own github directory
    github: 'tmpworldwide/tmp-magic-bullet',
    //this lets us know which files to highlight, to open on first visit
    opened: ['tmpmagicbullet', 'gdpr', 'a11y'],
    //this will show any comments that are relevant to a particular file
    comments: {
      'C:\\Users\\mspellac\\Google Drive\\Git\\tmp-magic-bullet\\init.js': `This is the file that is initiated first. This will load individual script based on the data attribute that is passed in e.g. data-a11y="true" or data-gdpr="true".`,
    },
    //the whole directory structure and name of the project
    tree,
    name
  },
  mutations: {
    toggleOpened(state, name) {
      if (state.opened.includes(name)) {
        state.opened = state.opened.filter(e => e !== name)
      } else {
        state.opened.push(name)
      }
    },
    toggleDark(state) {
      state.dark = !state.dark
    }
  }
})
